<!--服务质量过程监控-汇总-->
<template>
  <div class="gather">
    <!-- 故障报修 -->
    <div
      class="fault main-style"
      v-loading="!resGzbxCharts"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">故障报修</div>
      <div class="charts_count">
        <countLabel
          :fontSize="14"
          v-if="resGzbxCharts"
          :countData="countGzbxData"
          :countUnit="countGzbxUnit"
          :textAlign="'flex-start'"
        ></countLabel>
      </div>
      <div class="bar_charts">
        <bda-3d-barCharts
          v-if="resGzbxCharts"
          :chartsConfig="gzbxChartsConfig"
          :barType="gzbxbarType"
          :xVertical="true"
          :barWidth="2"
          :isSmallText="true"
        ></bda-3d-barCharts>
      </div>
    </div>
    <!-- 停电信息 -->
    <div
      class="outage main-style"
      v-loading="!resTdxxCharts"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">停电信息发布量</div>
      <div class="charts_count">
        <countLabel
          :fontSize="14"
          v-if="resTdxxCharts"
          :countData="countTdxxData"
          :countUnit="countTdxxUnit"
          :textAlign="'flex-start'"
        ></countLabel>
      </div>
      <div class="bar_charts">
        <bda-3d-barCharts
          v-if="resTdxxCharts"
          :chartsConfig="tdxxChartsConfig"
          :barType="tdxxbarType"
          :xVertical="true"
          :barWidth="2"
          :isSmallText="true"
        ></bda-3d-barCharts>
      </div>
    </div>
    <!-- 投诉业务量 -->
    <div
      class="compliant main-style"
      v-loading="!resTsCharts"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">投诉业务量</div>
      <div class="charts_count">
        <countLabel
          :fontSize="14"
          v-if="resTsCharts"
          :countData="countTsData"
          :countUnit="countTsUnit"
          :textAlign="'flex-start'"
        ></countLabel>
      </div>
      <div class="bar_charts">
        <bda-3d-barCharts
          v-if="resTsCharts"
          :chartsConfig="tsChartsConfig"
          :barType="tsbarType"
          :xVertical="true"
          :barWidth="2"
          :isSmallText="true"
        ></bda-3d-barCharts>
      </div>
    </div>
    <!-- 供电质量 -->
    <div
      class="power main-style"
      v-loading="!resGdzlCharts"
      element-loading-background="rgba(0, 0, 0, 0.2)"
      element-loading-text="加载中"
    >
      <div class="title">供电质量</div>
      <div class="charts_count">
        <countLabel
          :fontSize="14"
          v-if="resGdzlCharts"
          :countData="countGdzlData"
          :countUnit="countGdzlUnit"
          :textAlign="'flex-start'"
        ></countLabel>
      </div>
      <div class="bar_charts">
        <bda-3d-barCharts
          v-if="resGdzlCharts"
          :chartsConfig="gdzlChartsConfig"
          :barType="gdzlbarType"
          :xVertical="true"
          :barWidth="2"
          :isSmallText="true"
        ></bda-3d-barCharts>
      </div>
    </div>
  </div>
</template>

<script>
import bda3dBarCharts from '../../../widgets/bda-3d-barCharts/bda-3d-barCharts'
// import barCharts from "@/components/barCharts/barCharts"
import countLabel from "@/components/countLabel/countLabel"
import {
  dataChange,
  dataFilter,
  translateFilter
} from '../../../commonJS/dataChanges'
// import {dataChange} from '../../../commonJS/dataChanges'

import config from '../../../base/mixin/service-quality-process';

export default {
  name: "gather",
  mixins: [config],
  components: {
    // barCharts,
    countLabel,
    bda3dBarCharts
  },
  props: {
    // lineShow: {
    //     type: Boolean,
    //     required: true
    // }
  },
  data() {
    return {
      // 柱状图数据(故障报修)是否返回
      resGzbxCharts: false,
      // 柱状图数据(停电信息)是否返回
      resTdxxCharts: false,
      // 柱状图数据(投诉)是否返回
      resTsCharts: false,
      // 柱状图数据(供电质量)是否返回
      resGdzlCharts: false,
      //故障报修统计数据单位
      countGzbxUnit: '',
      //故障报修统计数据
      countGzbxData: [],
      //停电信息统计数据单位
      countTdxxUnit: '',
      //停电信息统计数据
      countTdxxData: [],
      //投诉统计数据单位
      countTsUnit: '',
      //投诉统计数据
      countTsData: [],
      //供电质量数据单位
      countGdzlUnit: '',
      //供电质量统计数据
      countGdzlData: [],
      //故障报修图表数据
      gzbxChartsConfig: {},
      gzbxbarType: 'stack',
      //停电信息图表数据
      tdxxChartsConfig: {},
      tdxxbarType: 'stack',
      //投诉图表数据
      tsChartsConfig: {},
      tsbarType: 'stack',
      //供电质量图表数据
      gdzlChartsConfig: {},
      gdzlbarType: 'stack',
    }
  },
  watch: {},
  mounted() {

  },
  created() {
    //停电信息
    this.volumeForPowerCut({
      orgType: this.$parent.orgType,
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    }).then(res => {
      this.tdxxFilter(res)
    }).catch(() => {
      this.resTdxxCharts = true;
    })
    //  投诉业务量
    this.volumeForComplain({
      orgType: this.$parent.orgType,
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    }).then(res => {
      this.tsFilter(res);
    }).catch(() => {
      this.resTsCharts = true;
    })
    //故障业务量
    this.volumeForMalfunction({
      orgType: this.$parent.orgType,
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    }).then(res => {
      this.gzbxFilter(res)
    }).catch(() => {
      this.resGzbxCharts = true;
    });
    //供电质量业务量
    this.volumeForQuality({
      orgType: this.$parent.orgType,
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    }).then(res => {
      this.gdzlFilter(res)
    }).catch(() => {
      this.resGdzlCharts = true;
    })
  },
  methods: {

    //去重函数
    unique(arr) {
      return Array.from(new Set(arr))
    },
    //故障报修业务量
    gzbxFilter(data) {
      if (data && data.length !== 0) {
        //统计数据处理
        this.countGzbxUnit = '件'
        let color = ['#00E7FF', '#00FFD4', '#86FF80']
        var j = -1
        this.countGzbxData = data.count.map((e, i) => {
          if (i <= color.length) {
            j++
          } else {
            j = -1
          }
          return {
            key: e.isdone,
            value: e.ccsum,
            color: color[j]
          }

        })
        if (this.countGzbxData.findIndex(e => { return e.key === '1' }) === -1) {
          this.countGzbxData.push({
            key: '1',
            value: 0,
            color: '#00E7FF'

          })
        }
        if (this.countGzbxData.findIndex(e => { return e.key === '0' }) === -1) {
          this.countGzbxData.push({
            key: '0',
            value: 0,
            color: '#00FFD4'
          })
        }
        let count = 0
        this.countGzbxData.forEach(e => {
          if (e.key === '1') {
            e.showText = '处理完成'
            count = count + e.value
          }
          if (e.key === '0') {
            e.showText = '处理中'
            count = count + e.value
          }
        })
        this.countGzbxData.unshift({
          key: '3',
          value: count,
          showText: '业务量'
        })

        //柱状图数据处理
        let _data = translateFilter(data.line, this.$parent.orgTranslate, 'orgno', 'orgName');
        let barCharts = dataChange(_data, 'orgno', 'orgName');

        //图表颜色
        let colorMap = {
          '处理中': ['#009DFF', '#005FFF'],
          '处理完成': ['#00E5FF', '#00ABFF']
        }
        let nData = dataFilter(barCharts, {
          name: 'isdone',
          translate: {
            '0': '处理中',
            '1': '处理完成'
          },
          valueField: 'sum'
        }, 'orgName');

        nData.colorMap = colorMap
        this.gzbxChartsConfig = nData

        this.$nextTick(() => {
          this.resGzbxCharts = true;
        })
      }

    },
    //停电信息业务量
    tdxxFilter(data) {
      if (data && data.length !== 0) {
        //统计数据处理
        this.countTdxxUnit = '条'
        this.countTdxxData = [];
        let color = ['#00E7FF', '#00FFD4', '#86FF80']
        var j = -1
        data.count.forEach((e, i) => {
          // if (e.status === '01' || e.status === '03' || e.status === '04') {
          if (i <= color.length) {
            j++
          } else {
            j = -1
          }
          this.countTdxxData.push({
            key: e.status,
            value: e.ccsum,
            color: color[j]

          })
          // }
        })

        if (this.countTdxxData.findIndex(e => { return e.key === '01' }) === -1) {
          this.countTdxxData.push({
            key: '01',
            value: 0,
            color: '#00E7FF'

          })
        }
        if (this.countTdxxData.findIndex(e => { return e.key === '02' }) === -1) {
          this.countTdxxData.push({
            key: '02',
            value: 0,
            color: '#86FF80'

          })
        }
        if (this.countTdxxData.findIndex(e => { return e.key === '03' }) === -1) {
          this.countTdxxData.push({
            key: '03',
            value: 0,
            color: '#00FFD4'
          })
        }
        // this.countTdxxData = data.count.map(e => {
        //     return {
        //         key: e.status,
        //         value: e.ccsum
        //     }
        // })
        let count = 0
        this.countTdxxData.forEach(e => {
          if (e.key === '01') {
            e.showText = '全部送电'
            count = count + e.value
          }

          if (e.key === '02') {
            e.showText = '部分送电'
            count = count + e.value
          }
          if (e.key === '03') {
            e.showText = '未送电'
            count = count + e.value

          }
        })
        this.countTdxxData.forEach((e, i) => {
          if (e.key === '04') {
            this.countTdxxData.splice(i, 1)
          }
        })
        // this.countTdxxData.unshift({
        //     key: '05',
        //     value: count,
        //     showText: '发布量'
        // })
        let _data = translateFilter(data.line, this.$parent.orgTranslate, 'orgno', 'orgName');
        let barCharts = dataChange(_data, 'orgno', 'orgName');

        //图表颜色 正执行 全部送电 未送电
        let colorMap = {
          '全部送电': ['#009DFF', '#005FFF'],
          '部分送电': ['#00E5FF', '#00ABFF'],
          '未送电': ['#00FEBB', '#00F8FF'],
          // '部分送电': ['#ef5167', '#c8253c']
        }
        //
        barCharts = this.barChartsStatus(barCharts)

        let nData = dataFilter(barCharts, {
          name: 'status',
          translate: {
            // '04': '正执行',
            '03': '未送电',
            '02': '部分送电',
            '01': '全部送电'
          },
          valueField: 'ccsum'
        }, 'orgName');
        window.console.log(nData, 'nData');
        nData.colorMap = colorMap;
        this.tdxxChartsConfig = nData;
        this.resTdxxCharts = false;
        this.$nextTick(() => {
          this.resTdxxCharts = true;
        })
      }

    },
    //投诉业务量
    tsFilter(data) {
      if (data && data.length !== 0) {
        //统计数据处理
        this.countTsUnit = '件'
        let color = ['#00E7FF', '#00FFD4', '#86FF80']
        var j = -1
        this.countTsData = data.count.map((e, i) => {
          if (i <= color.length) {
            j++
          } else {
            j = -1
          }
          return {
            key: e.status,
            value: e.ccsum,
            color: color[j]
          }

        })
        if (this.countTsData.findIndex(e => { return e.key === '01' }) === -1) {
          this.countTsData.push({
            key: '01',
            value: 0,
            color: '#00E7FF'

          })
        }
        if (this.countTsData.findIndex(e => { return e.key === '04' }) === -1) {
          this.countTsData.push({
            key: '04',
            value: 0,
            color: '#00FFD4'
          })
        }
        let count = 0
        this.countTsData.forEach(e => {
          if (e.key === '01') {
            e.showText = '处理完成'
            count = count + e.value
          }

          if (e.key === '03') {
            e.showText = '未送电'
            count = count + e.value

          }
          if (e.key === '04') {
            e.showText = '处理中'
            count = count + e.value
          }
        })
        this.countTsData.forEach((e, i) => {
          if (e.key === '02') {
            this.countTsData.splice(i, 1)
          }
        })

        this.countTsData.unshift({
          key: '05',
          value: count,
          showText: '业务量'
        })
        //柱状图数据处理
        let _data = translateFilter(data.line, this.$parent.orgTranslate, 'orgno', 'orgName');
        let barCharts = dataChange(_data, 'orgno', 'orgName');
        //图表颜色
        let colorMap = {
          '处理完成': ['#00FEBB', '#00BDFA'],
          '处理中': ['#A7FF89', '#67FFDB']
        }

        barCharts = this.barChartsStatus(barCharts)

        let nData = dataFilter(barCharts, {
          name: 'status',
          translate: {
            '04': '处理中',
            //  '03': '未送电',
            //  '02': '未处理',
            '01': '处理完成'
          },
          valueField: 'ccsum'
        }, 'orgName')

        nData.colorMap = colorMap;
        this.resTsCharts = false;
        this.tsChartsConfig = nData;
        this.$nextTick(() => {
          this.resTsCharts = true;
        })
      }


    },
    //供电质量
    gdzlFilter(data) {

      if (data && data.length !== 0) {
        //统计数据处理
        this.countGdzlUnit = '件'
        let color = ['#00E7FF', '#00FFD4', '#86FF80']
        var j = -1
        this.countGdzlData = data.count.map((e, i) => {
          if (i <= color.length) {
            j++
          } else {
            j = -1
          }
          return {
            key: e.status,
            value: e.ccsum,
            color: color[j]
          }

        })
        if (this.countGdzlData.findIndex(e => { return e.key === '01' }) === -1) {
          this.countGdzlData.push({
            key: '01',
            value: 0,
            color: '#00E7FF'

          })
        }
        if (this.countGdzlData.findIndex(e => { return e.key === '04' }) === -1) {
          this.countGdzlData.push({
            key: '04',
            value: 0,
            color: '#00FFD4'
          })
        }
        let count = 0
        this.countGdzlData.forEach(e => {
          if (e.key === '01') {
            e.showText = '处理完成'
            count = count + e.value
          }

          if (e.key === '03') {
            e.showText = '未送电'
            count = count + e.value

          }
          if (e.key === '04') {
            e.showText = '处理中'
            count = count + e.value
          }
        })
        this.countGdzlData.forEach((e, i) => {
          if (e.key === '02') {
            this.countGdzlData.splice(i, 1)
          }
        })

        this.countGdzlData.unshift({
          key: '05',
          value: count,
          showText: '业务量'
        })
        //图表数据处理
        let _data = translateFilter(data.line, this.$parent.orgTranslate, 'orgno', 'orgName');
        let barCharts = dataChange(_data, 'orgno', 'orgName');

        //图表颜色
        let colorMap = {
          '处理完成': ['#3395FF', '#3170FF'],
          '处理中': ['#00E5FF', '#00ABFF'],
          //     '未送电': ['#743fa1', '#5500a1'],
          //     '未处理': ['#ef5167', '#c8253c']
        }
        barCharts = this.barChartsStatus(barCharts)
        let nData = dataFilter(barCharts, {
          name: 'status',
          translate: {
            '04': '处理中',
            //   '03': '未送电',
            //    '02': '未处理',
            '01': '处理完成'
          },
          valueField: 'ccsum'
        }, 'orgName');
        nData.colorMap = colorMap
        this.gdzlChartsConfig = nData;
        this.resGdzlCharts = false;
        this.$nextTick(() => {
          this.resGdzlCharts = true;
        })
      }
    },

    //状态数据处理
    barChartsStatus(barCharts) {
      barCharts.forEach(e => {
        let isStatus1 = false
        let isStatus2 = false
        let isStatus3 = false
        let isStatus4 = false
        e.data.forEach(e2 => {
          if (e2.status === '01') {
            isStatus1 = true
          }
          if (e2.status === '02') {
            isStatus2 = true
          }
          if (e2.status === '03') {
            isStatus3 = true
          }
          if (e2.status === '04') {
            isStatus4 = true
          }
        })

        if (!isStatus1) {
          e.data.push({
            orgno: e.name,
            ccsum: 0,
            status: '01'
          })
        }
        if (!isStatus2) {
          e.data.push({
            orgno: e.name,
            ccsum: 0,
            status: '02'
          })
        }
        if (!isStatus3) {
          e.data.push({
            orgno: e.name,
            ccsum: 0,
            status: '03'
          })
        }
        if (!isStatus4) {
          e.data.push({
            orgno: e.name,
            ccsum: 0,
            status: '04'
          })
        }
      })

      return barCharts
    }
  },
}
</script>

<style lang="scss" scoped>
.gather {
  width: 100%;
  display: flex;
  //  justify-content: space-around;
  height: 100%;
  color: #fff;

  .main-style {
    width: calc(25%);
    padding: 0 20px;
    box-sizing: border-box;
    background: url("../../../assets/img/监控框4.png") no-repeat top center;
    background-size: 100% 100%;
  }

  //   .fault {
  //     flex: 1;
  //   }

  //   .outage {
  //     flex: 1;
  //   }

  //   .compliant {
  //     flex: 1;
  //   }

  //   .power {
  //     flex: 1;
  //   }
  .title {
    width: 100%;
    color: #00e7ff;
    font-size: 18px;
    height: 15%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }

  .charts_count {
    display: flex;
    height: 13%;
    justify-content: center;
    align-items: center;
  }

  .bar_charts {
    height: calc(100% - 15% - 13%);
  }
}
</style>