// 数据内格式转换
export let dataChange = (data, field, fzField, istrue) => {
    let dataMao = {}, arr = [], fzFieldMap = {};
    if (data && data.length !== 0) {
        for (let item of data) {
            if (item[field]) {
                if (!dataMao[item[field]]) {
                    fzFieldMap[item[field]] = item[fzField]  //{‘50412’:'江津'}
                    dataMao[item[field]] = [item]; //{‘50412’:[]}
                } else {
                    dataMao[item[field]].push(item)
                }
            }
        }
        if (dataMao) {
            for (let key in dataMao) {
                let _obj = {
                    name: key,
                    data: dataMao[key]
                };

                //数据为空的补零
                if (istrue) {
                    let have04 = false;
                    _obj.data.forEach(item => {
                        if (item.status == '03') {
                            have04 = true;
                            return;
                        }
                    })
                    if (have04 == false) {
                        _obj.data.push({ status: '03', ccsum: 0 })
                    }

                    let have01 = false;
                    _obj.data.forEach(item => {
                        if (item.status == '01') {
                            have01 = true;
                            return;
                        }
                    })
                    if (have01 == false) {
                        _obj.data.push({ status: '01', ccsum: 0 })
                    }
                }
                //数据为空的补零

                if (fzField) {
                    _obj[fzField] = fzFieldMap[key];
                }

                arr.push(_obj)
            }
        }
        return arr;
    }
};
//补齐数据---有的数据点是不够指标数
let putDatas = (arr, objs) => {
    // window.console.log(arr,'_info333333333333')
    let wdLen = objs.translate ? Object.keys(objs.translate).length : 0;
    if (arr) {
        for (let item of arr) {
            let _vMap = {}
            if (item.data && item.data.length < wdLen) {
                for (let sys of item.data) {
                    _vMap[sys[objs.name]] = sys[objs.name];
                }
                for (let k in objs.translate) {
                    if (!_vMap[k]) {
                        let _addObj = {};
                        _addObj[objs.name] = k;
                        _addObj[objs.valueField] = 0;
                        item.data.push(_addObj)
                    }
                }
            }
        }
    }
    return arr
}
// 数据格式归纳(指标归纳对应)---针对echarts
export let dataFilter = (data, quotaObj, zbFildName) => {
    let dimension = [];  //统计维度信息
    let quotaMap = {};    //指标归类信息
    let arr = putDatas(data, quotaObj);
    if (arr && arr.length !== 0) {
        for (let item of arr) {
            dimension.push(item[zbFildName]);
            if (item.data) {
                for (let sys of item.data) {
                    if (sys[quotaObj.name]) {
                        let fy = quotaObj.translate[sys[quotaObj.name]];
                        if (fy) {
                            if (!quotaMap[fy]) {
                                quotaMap[fy] = [sys[quotaObj.valueField]]
                            } else {
                                quotaMap[fy].push(sys[quotaObj.valueField])
                            }
                        }

                    }
                }
            }
        }
    }
    return {
        dimension: dimension,
        quotaMap: quotaMap
    }
}

// 数据翻译
export let translateFilter = (data, translateMap, filed, mbFiledName) => {
    if (data) {
        for (let item of data) {
            let _transInfo = '';
            if (item[filed]) {
                _transInfo = translateMap[item[filed]] ? translateMap[item[filed]] : item[filed]
            }
            item[mbFiledName] = _transInfo;
        }
    }
    return data;
}

