<template>
  <div class="barCharts-style">
    <div :id="id" class="charts-main"></div>
  </div>
</template>

<script>
/**
 * @3d柱状图组件
 * @author：beixian.huang
 * @参数说明：
 * chartsConfig：{
 *     dimension:['xxxx','22222'],    //x轴对应的数据值
 *     quotaMap:{
 *      '抢修类'：[11,1232,111],
 *      '非抢修类'：[11,1232,111],
 *     }，   // 指标名为key,数据值为数组
 *     colorMap：{       // 颜色值数据
 *        '非抢修类':['#31fff1','#27ccc1'], //第一个颜色为实体颜色，第二个为阴影颜色
          '抢修类':['#0088d1','#025b95']
 *     }
 * }  必须
 * barType：stack/bar 为柱状图的模式，stack是堆叠模式（调整完），bar为普通模式（目前只支持单维度的模式）
 * barWidth：柱状图的宽度。
 * legendStatus：图例信息是否展示true/false, 默认false
 * yAxiosDw : y轴的单位信息，例“%”
 * changeAxios：x,y轴是否交换 true/false, 默认false
 * xVertical：x轴的文字展示信息是否单字（类回车）显示格式。
 *showType：是否3d图。(false)
 *isDiffColor:各个柱子是否不同颜色,默认为否,相同
 * percentRange :数值是否最大为100%,默认为否,不设置最大值
 * isScroll:是否自动滚动
 * */
import echarts from 'echarts';

export default {
  name: "bda-3d-barCharts",
  props: {
    chartsConfig: {
      type: Object,
      default: () => {
        return {}
      }
    },
    barType: {
      type: String,
      default: () => {
        return 'stack'
      }
    },
    barWidth: {
      type: Number,
      default: () => {
        return 3
      }
    },
    customizedStyle: {
      type: Number,
      default: () => {
        return 1
      }
    },
    legendStatus: {
      type: Boolean,
      default: () => {
        return true
      }
    },
    yAxiosDw: {
      type: String,
      default: () => {
        return ''
      }
    },
    changeAxios: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    xVertical: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    showType: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    isDiffColor: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    percentRange: {
      type: Array,
      default: () => {
        return []
      }
    },
    isScroll: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    isSmallText: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    fontSizeShow: {
      type: Number,
      default: () => {
        return 0
      }
    }
  },
  data() {
    return {
      id: Math.random() + '_',
      legend: [],
      dataDetails: [],
      xData: [],
      series: [],
      drp: 3,
      fontSize: this.fontSizeShow !== 0 ? this.fontSizeShow : 15
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    this.xData = this.chartsConfig.dimension ? this.chartsConfig.dimension : [];
    let _winWidth = document.body.clientWidth;
    if (_winWidth > 1700) {
      if (this.xData.length <= 10) {
        this.drp = this.barWidth
      } else {
        this.drp = this.barWidth * 0.5
      }
    } else if (_winWidth <= 1700 && _winWidth >= 1400) {
      if (this.xData.length <= 10) {
        // this.fontSize = 14;
        this.fontSize = 14;
        this.drp = this.barWidth * 0.4;
      } else {
        this.fontSize = 14;
        this.drp = this.barWidth * 0.3;
      }
    } else {
      if (this.xData.length <= 10) {
        this.fontSize = 14;
        this.drp = this.barWidth * 0.3;
      } else {
        this.fontSize = 12;
        this.drp = this.barWidth * 0.2;
      }
    }
    if (this.isSmallText && this.xData.length > 10) this.fontSize = 10
    this.filterData(this.barType, this.chartsConfig.quotaMap, this.chartsConfig.colorMap);
    this.$nextTick(() => {
      this.init();
    })
  },
  methods: {
    filterData(type, data, colorMap) {
      let series = [];
      let count = -1;
      let dpr = this.drp;
      let that = this
      if (data) {
        window.console.log(data, 'chartsConfig')
        for (let key in data) {
          this.dataDetails.push(data[key]);
          this.legend.push(key);
          let colorGl = colorMap[key]
          count++;
          let _series = {
            type: 'bar',
            name: key,
            barWidth: 8 * dpr,
            itemStyle: {
              normal: {
                color:
                  new echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      { offset: 0, color: colorGl[0], },
                      // { offset: 0.5, color: colorGl[1], },                    //柱图渐变色
                      { offset: 1, color: colorGl[1], },                 //柱图渐变色
                    ]
                  )
                ,
                barBorderRadius: [0, 0, 0, 0]
              }
            },
            data: this.dataDetails[count],
          };
          if (that.isDiffColor) {
            _series.itemStyle.normal.color =
              (params) => {
                if (that.isDiffColor) {
                  var colorList = colorGl;
                  var colorItem = colorList[params.dataIndex];
                  return new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                    { offset: 0, color: colorItem[0], },
                    { offset: 0.5, color: colorItem[1], },                    //柱图渐变色
                    { offset: 1, color: colorItem[1], },
                  ], false);

                }
              }
          }
          let _glSeries = {
            name: key,
            tooltip: {
              show: false
            },
            type: 'bar',
            barWidth: 2 * dpr,
            itemStyle: {
              normal: {
                color: colorGl[1],
                barBorderRadius: [0, 0, 0, 0]
              }
            },
            data: this.dataDetails[count],

            legendHoverLink: false
          };
          if (type === 'stack') {
            _series.stack = 'stack';
            if (this.showType) _glSeries.stack = 'stack2';
            if (this.showType) _glSeries.barGap = 0;
          }
          series.push(_series);
          if (this.showType) series.push(_glSeries)
        }
      }
      this.series = series;
    },
    init() {
      let chartsDom = document.getElementById(this.id);
      let charts = echarts.init(chartsDom);
      let xData = this.xData;
      let $this = this;
      let xAxios = {
        data: xData,
        axisLabel: {
          interval: 0,
          show: true,
          fontSize: this.fontSize + 2,
          fontFamily: 'Noto Sans SC',
          formatter: function (value) {

            if (xData.length > 10 || $this.xVertical) {

              return value.split("").join("\n");
            } else {
              if ($this.changeAxios) {
                if (value.length > 4) {
                  return value.substring(0, 4) + '...'
                } else {
                  return value
                }
              } else {
                var newParamsName = ""; // 最终拼接成的字符串
                var paramsNameNumber = value.length; // 实际标签的个数
                var provideNumber = 2; // 每行能显示的字的个数
                if ($this.barWidth > 4) {
                  provideNumber = 4
                }
                var rowNumber = Math.ceil(paramsNameNumber / provideNumber); // 换行的话，需要显示几行，向上取整
                /**
                 * 判断标签的个数是否大于规定的个数， 如果大于，则进行换行处理 如果不大于，即等于或小于，就返回原标签
                 */
                // 条件等同于rowNumber>1
                if (paramsNameNumber > provideNumber) {
                  /** 循环每一行,p表示行 */
                  for (var p = 0; p < rowNumber; p++) {
                    var tempStr = ""; // 表示每一次截取的字符串
                    var start = p * provideNumber; // 开始截取的位置
                    var end = start + provideNumber; // 结束截取的位置
                    // 此处特殊处理最后一行的索引值
                    if (p == rowNumber - 1) {
                      // 最后一次不换行
                      tempStr = value.substring(start, paramsNameNumber);
                    } else {
                      // 每一次拼接字符串并换行
                      tempStr = value.substring(start, end) + "\n";
                    }
                    newParamsName += tempStr; // 最终拼成的字符串
                  }

                } else {
                  // 将旧标签的值赋给新标签
                  newParamsName = value;
                }
                //将最终的字符串返回
                return newParamsName
              }
            }
          }
        },
        splitLine: {
          show: false,
          lineStyle: {
            color: 'rgba(255,255,255,.1)'
          }
        },
        axisLine: {
          lineStyle: {
            color: '#fff',
            fontSize: this.fontSize
          }
        }
      };
      let _width = document.body.clientWidth;
      if (_width <= 1700 && _width >= 1400) {
        if (xData.length > 8 && xData.length <= 10) {
          xAxios.axisLabel.fontSize = this.fontSize * 0.9;
        } else if (xData.length > 10) {
          xAxios.axisLabel.fontSize = this.fontSize * 0.8;
        }
        if (this.customizedStyle !== 1) {
          xAxios.axisLabel.fontSize = this.fontSize * 0.74;
        }
      }
      let dw = this.yAxiosDw;
      let options = {
        tooltip: {
          formatter: function (a) {
            return a.name + '<br>' + a.seriesName + '：' + a.value + dw
          }
        },
        grid: {
          top: this.legendStatus ? 30 : 10,
          left: 50,
          right: '5%',
          bottom: !this.isScroll ? '25%' : '30%',
        },
        legend: {
          show: this.legendStatus,
          data: this.legend,
          textStyle: {
            color: '#fff',
            fontFamily: 'Noto Sans SC',
            fontSize: this.fontSize,
          },
          right: 20,
          top: 0
        },
        dataZoom: this.isScroll ? [//滑动条
          {
            xAxisIndex: 0,//这里是从X轴的0刻度开始
            show: false,//是否显示滑动条，不影响使用
            type: 'slider', // 这个 dataZoom 组件是 slider 型 dataZoom 组件
            startValue: 0, // 从头开始。
            endValue: 9  // 一次性展示6个。
          }
        ] : [],

        xAxis: xAxios,
        yAxis: {
          type: 'value',
          splitLine: {
            show: true,
            lineStyle: {
              color: 'rgba(255,255,255,.1)'
            }
          },
          axisLine: {
            lineStyle: {
              color: '#fff'
            }
          },
          axisLabel: {
            show: true,
            fontSize: this.fontSize,
            formatter: function (value) {
              return value + dw
              // if (value>=1000){
              //     if (value%1000===0){
              //         return (value/1000)+'K'+dw
              //     }else {
              //         return (value/1000).toFixed(2)+'K'+dw
              //     }
              // }else {
              //     return value+dw
              // }
            },
            interval: 0,
            rotate: 0
          }
        },

        series: this.series
      };
      if (this.percentRange) options.yAxis.max = this.percentRange[1]
      if (this.percentRange) options.yAxis.min = this.percentRange[0]
      if (this.changeAxios === true) {
        options.xAxis = options.yAxis;
        options.yAxis = xAxios;
        let xLen = 0;
        if (xData) {
          for (let v of xData) {
            if (v.length >= xLen) {
              xLen = v.length;
            }
          }
        }
        if (xLen >= 4) {
          options.grid.left = 80;
        } else {
          options.grid.left = 50;
        }
        if (this.legendStatus === false) {
          options.grid.top = 10;
        }
      }
      // charts.resize()
      charts.setOption(options)

      window.console.log(options, 'options222')
      if (this.isScroll) {
        let start = 0
        let end = 9
        setInterval(() => {
          if (end <= this.dataDetails[0].length) {
            charts.setOption({
              dataZoom: {
                startValue: start++,
                endValue: end++
              }
            })
          } else {
            start = 0
            end = 9
          }
        }, 2000)
      }
    }
  }
}
</script>

<style scoped lang="scss">
.barCharts-style {
  position: relative;
  width: 100%;
  height: 100%;

  .noDataStyle {
    line-height: 50px;
    font-size: 14px;
    text-align: center;
  }

  .charts-main {
    width: 100%;
    height: 100%;
  }

  .legend-style {
    position: absolute;
    right: 24px;
    font-size: 14px;
    top: 10px;
    z-index: 999;

    label {
      display: block;
      float: left;
      margin-right: 10px;
      cursor: pointer;

      .colorPick {
        width: 20px;
        height: 10px;
        display: block;
        float: left;
        border-radius: 3px;
        margin-right: 5px;
        margin-top: 5px;
        cursor: pointer;
      }

      .keyName {
        display: block;
        float: left;
        cursor: pointer;
      }
    }
  }
}
</style>